.quiz-app {
  height: 100%;
  overflow-y: auto;
  padding: 1rem;
}

/* Scrollbar styling for the quiz app container */
.quiz-app::-webkit-scrollbar {
  width: 8px;
}

.quiz-app::-webkit-scrollbar-track {
  background: transparent;
}

.quiz-app::-webkit-scrollbar-thumb {
  background: var(--glass-border-color);
  border-radius: 4px;
}

.quiz-app::-webkit-scrollbar-thumb:hover {
  background: var(--primary);
}

.quiz-generator,
.quiz-container,
.quiz-result,
.quiz-history {
  max-width: 800px;
  margin: 0 auto;
  background: rgba(15, 23, 42, 0.6);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
}

/* Add a subtle glow effect */
.quiz-generator::before,
.quiz-container::before,
.quiz-result::before,
.quiz-history::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--primary), transparent);
  opacity: 0.5;
}

.quiz-generator h2,
.quiz-container h2,
.quiz-result h2,
.quiz-history h2 {
  margin-bottom: 2rem;
  color: var(--text-main);
  text-align: center;
  font-size: 2rem;
  letter-spacing: -1px;
  text-shadow: 0 0 20px rgba(6, 182, 212, 0.3);
}

.quiz-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 600;
  color: var(--primary);
  font-family: 'Fira Code', monospace;
  font-size: 0.9rem;
  letter-spacing: 1px;
}

.form-group input,
.form-group select {
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  font-size: 1rem;
  color: var(--text-main);
  transition: all 0.3s ease;
  font-family: 'Inter', sans-serif;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 15px rgba(6, 182, 212, 0.2);
  background: rgba(6, 182, 212, 0.05);
}

.generate-button,
.history-button,
.retry-button,
.nav-button,
.submit-button,
.back-button {
  background: transparent;
  color: var(--primary);
  border: 1px solid var(--primary);
  padding: 1rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Fira Code', monospace;
  position: relative;
  overflow: hidden;
}

.generate-button:hover,
.history-button:hover,
.retry-button:hover,
.nav-button:hover,
.submit-button:hover,
.back-button:hover {
  background: rgba(6, 182, 212, 0.1);
  box-shadow: 0 0 20px rgba(6, 182, 212, 0.4);
  text-shadow: 0 0 8px var(--primary);
}

.generate-button:disabled,
.nav-button:disabled,
.submit-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  box-shadow: none;
  border-color: var(--text-muted);
  color: var(--text-muted);
}

.submit-button {
  border-color: var(--secondary);
  color: var(--secondary);
}

.submit-button:hover:not(:disabled) {
  background: rgba(217, 70, 239, 0.1);
  box-shadow: 0 0 20px rgba(217, 70, 239, 0.4);
  text-shadow: 0 0 8px var(--secondary);
}

.quiz-header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding-bottom: 1.5rem;
}

.quiz-progress {
  color: var(--text-muted);
  font-family: 'Fira Code', monospace;
  font-size: 0.9rem;
}

.question-header h3 {
  color: var(--text-main);
  font-size: 1.4rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.hint-button {
  background: rgba(251, 191, 36, 0.1);
  border: 1px solid rgba(251, 191, 36, 0.5);
  color: #fbbf24;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'Fira Code', monospace;
  transition: all 0.3s ease;
}

.hint-button:hover:not(:disabled) {
  background: rgba(251, 191, 36, 0.2);
  box-shadow: 0 0 15px rgba(251, 191, 36, 0.3);
}

.hint-popup {
  background: rgba(251, 191, 36, 0.1);
  border: 1px solid rgba(251, 191, 36, 0.3);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
  color: #fbbf24;
}

.hint-popup button {
  background: transparent;
  border: 1px solid #fbbf24;
  color: #fbbf24;
  padding: 0.25rem 0.75rem;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 0.5rem;
  font-size: 0.8rem;
}

.option-label {
  display: flex;
  align-items: center;
  padding: 1.25rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.02);
  margin-bottom: 0.75rem;
}

.option-label:hover {
  border-color: var(--primary);
  background: rgba(6, 182, 212, 0.05);
  transform: translateX(5px);
}

.option-label.selected {
  border-color: var(--primary);
  background: rgba(6, 182, 212, 0.1);
  box-shadow: 0 0 15px rgba(6, 182, 212, 0.1);
}

.option-label input {
  margin-right: 1rem;
  accent-color: var(--primary);
}

.question-dots {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.5rem 0;
}

.question-dot {
  width: 36px;
  height: 36px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: transparent;
  color: var(--text-muted);
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Fira Code', monospace;
  display: flex;
  align-items: center;
  justify-content: center;
}

.question-dot:hover {
  border-color: var(--primary);
  color: var(--primary);
}

.question-dot.active {
  border-color: var(--primary);
  background: rgba(6, 182, 212, 0.2);
  color: var(--primary);
  box-shadow: 0 0 10px rgba(6, 182, 212, 0.3);
}

.question-dot.answered {
  border-color: var(--secondary);
  color: var(--secondary);
}

.result-score h3 {
  font-size: 4rem;
  color: var(--primary);
  margin: 0;
  text-shadow: 0 0 30px rgba(6, 182, 212, 0.5);
  font-family: 'Fira Code', monospace;
}

.result-score p {
  font-size: 1.2rem;
  color: var(--text-muted);
}

.improvement-tips {
  background: rgba(217, 70, 239, 0.05);
  border: 1px solid rgba(217, 70, 239, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  margin: 2rem 0;
  text-align: left;
}

.improvement-tips h4 {
  color: var(--secondary);
  margin-bottom: 1rem;
  font-family: 'Fira Code', monospace;
  text-transform: uppercase;
}

.improvement-tips li {
  margin-bottom: 0.5rem;
  color: var(--text-main);
  list-style-type: square;
}

.history-item {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  transition: all 0.3s ease;
}

.history-item:hover {
  border-color: var(--primary);
  box-shadow: 0 0 20px rgba(6, 182, 212, 0.1);
  transform: translateY(-2px);
}

.history-score {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
  font-family: 'Fira Code', monospace;
  text-shadow: 0 0 10px rgba(6, 182, 212, 0.3);
}

.chart-card {
  background: rgba(15, 23, 42, 0.4);
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

@media (max-width: 768px) {
  .quiz-app {
    padding: 0.5rem;
  }

  .quiz-generator,
  .quiz-container,
  .quiz-result,
  .quiz-history {
    padding: 1.5rem;
  }

  .question-header {
    flex-direction: column;
    gap: 1rem;
  }

  .hint-button {
    width: 100%;
  }

  .quiz-navigation {
    flex-direction: column;
    gap: 1rem;
  }

  .nav-button,
  .submit-button {
    width: 100%;
  }
}